@import 'Functions';
@import 'Variables';
@import 'Mixins';

.article {
  &__img {
    padding: rem(5);
  }
  &__name {
    font-size: rem(32);
    font-weight: 400;
    text-transform: capitalize;
  }
  &__type {
    font-size: rem(20);
    &--item {
      font-size: rem(18);
      text-transform: capitalize;
    }
  }
}
